// Your custom styles
.bg-plantworks {
    background-color: #80d8ff;
    background: linear-gradient(100deg, #ffffff, #f2f2f2, #ff9800, #c9d9e9, #1976d2);
}

.bg-plantworks-component {
	background-color: #80d8ff;
	background: linear-gradient(100deg,#2096ff,#05ffa3)
}

.bg-plantworks-component-reverse {
	background-color: #80d8ff;
	background: linear-gradient(100deg,#05ffa3,#2096ff)
}

.grey-gradient {
	background: linear-gradient(40deg, #efefef, #eaeaea);
}

.reverse-grey-gradient {
	background: linear-gradient(40deg, #eaeaea, #efefef);
}

.main-shadow {
	box-shadow:0 2px 5px 0 rgba(0, 0, 0, 0.16), 0 2px 10px 0 rgba(0, 0, 0, 0.12);
}
