/* Bootstrap grid overrides to align with ember-paper */
$grid-breakpoints: (
  xs: 0,
  sm: 600px,
  md: 960px,
  lg: 1280px,
  xl: 1920px
);

$container-max-widths: (
  sm: 570px,
  md: 930px,
  lg: 1250px,
  xl: 1890px
);


/* Bootstrap font overrides */
$font-family:				"Noto Sans", sans-serif;
$font-family-sans-serif:	"Noto Sans", sans-serif;
$font-family-serif:			"Noto Serif", serif;
$font-family-monospace:		monospace;
$font-family-base:			$font-family-sans-serif;

/* Bootstrap color overrides */
$primary:					#1976d2;
$secondary:					#ff9800;
$success:					#81c784;
$info:						#80d8ff;
$warning:					#ffd740;
$danger:					#e65100;
$light:						#f8f9fa;
$dark:						#212529;

$theme-colors: (
  "primary": $primary,
  "secondary": $secondary,
  "success": $success,
  "info": $info,
  "warning": $warning,
  "danger": $danger,
  "light": $light,
  "dark": $dark
);

/* MDB color overrides */
$primary-color:				$primary;

$secondary-color:			$secondary;
$secondary-color-dark:		#ff8800;

$default-color:				$light;
$default-color-dark:		$dark;

$info-color:				$info;
$info-color-dark:			$primary;

$success-color:				$success;
$warning-color:				$warning;
$danger-color:				$danger;

/* Material Design Icons Font */
@import "ember-mdi";

/* Ember Power Select */
$ember-power-select-material-active-color: $primary;
@import "ember-power-select/themes/material";
@import "ember-power-select";

/* Bootstrap 4 */
@import "ember-bootstrap/bootstrap";

/* Material Design for Bootstrap 4 */
@import "scss/mdb.lite";

/* Ember Paper */
@import 'color-palette';
$default-color: '700';
$primary: $color-blue;

$default-color: '300';
$accent: $color-green;

$default-color: 'A200';
$warn: $color-amber;

@import 'ember-paper';
@import "ember-paper-expansion-panel";
@import "ember-paper-stepper";
@import 'paper-data-table';

$base-color: $dark;
@import "ember-paper-link";

/*
  * Generic utility classes.
  */

.overflow-none {
	overflow: hidden !important;
}

.overflow-scroll {
	overflow: scroll !important;
}

.overflow-auto {
	overflow: auto !important;
}

.overflow-x-none {
	overflow: hidden !important;
}

.overflow-x-scroll {
	overflow-x: scroll !important;
}

.overflow-x-auto {
	overflow-x: auto !important;
}

.overflow-y-none {
	overflow: hidden !important;
}

.overflow-y-scroll {
	overflow-y: scroll !important;
}

.overflow-y-auto {
	overflow-y: auto !important;
}

/* Bug-fixes :-( */
b, strong {
	font-weight: 600;
}

/* Overriding MDB Styles */
.table {
	margin-bottom: 0;
}

.classic-tabs .nav {
	border-radius: 0px;
}

.classic-tabs .nav li:first-child {
	margin-left: 0px;
}

.classic-tabs .nav li a {
	font-size: 0.9rem;
}

.nav-tabs .nav-item {
	margin-bottom: 0px;
}

.nav-tabs .nav-link {
	border: 0px;
}

.nav-tabs .nav-link.active {
	background-color: unset;
}

.tab-content {
	padding: 0px;
}

.table {
	margin-bottom: 0;
}

/*
 * Overriding ember-paper styles
 */

 md-input-container {
	margin-bottom: 0.5rem !important;
}

md-input-container .md-input {
	height: unset !important;
}

div.md-dialog-container {
	top: 60px;
	height: calc(100% - 75px);
}

