/*
 * Overriding jsTree styles
 */

 .jstree-default .jstree-node,
 .jstree-default .jstree-icon {
	 background-image: url("../img/32px.png");
 }

 .jstree-default .jstree-file {
	 background: url("../img/32px.png") -100px -68px no-repeat;
 }

 .jstree-default .jstree-folder {
	 background: url("../img/32px.png") -260px -4px no-repeat;
 }

 #jstree-dnd.jstree-default .jstree-ok,
 #jstree-dnd.jstree-default .jstree-er {
	 background-image: url("../img/32px.png");
 }

 .jstree-default-small .jstree-node,
 .jstree-default-small .jstree-icon {
	 background-image: url("../img/32px.png");
 }

 .jstree-default-small .jstree-file {
	 background: url("../img/32px.png") -103px -71px no-repeat;
 }

 .jstree-default-small .jstree-folder {
	 background: url("../img/32px.png") -263px -7px no-repeat;
 }

 #jstree-dnd.jstree-default-small .jstree-ok,
 #jstree-dnd.jstree-default-small .jstree-er {
	 background-image: url("../img/32px.png");
 }

 .jstree-default-large .jstree-node,
 .jstree-default-large .jstree-icon {
	 background-image: url("../img/32px.png");
 }

 .jstree-default-large .jstree-file {
	 background: url("../img/32px.png") -96px -64px no-repeat;
 }

 .jstree-default-large .jstree-folder {
	 background: url("../img/32px.png") -256px 0px no-repeat;
 }

 #jstree-dnd.jstree-default-large .jstree-ok,
 #jstree-dnd.jstree-default-large .jstree-er {
	 background-image: url("../img/32px.png");
 }

 @media (max-width: 768px) {
	 #jstree-dnd.jstree-dnd-responsive>.jstree-ok {
		 background-image: url("../img/40px.png");
		 background-position: 0 -200px;
		 background-size: 120px 240px;
	 }
	 #jstree-dnd.jstree-dnd-responsive>.jstree-er {
		 background-image: url("../img/40px.png");
		 background-position: -40px -200px;
		 background-size: 120px 240px;
	 }

	 .jstree-default-responsive .jstree-icon {
		 background-image: url("../img/40px.png");
	 }

	 .jstree-default-responsive .jstree-node,
	 .jstree-default-responsive .jstree-icon,
	 .jstree-default-responsive .jstree-node>.jstree-ocl,
	 .jstree-default-responsive .jstree-themeicon,
	 .jstree-default-responsive .jstree-checkbox {
		 background-image: url("../img/40px.png");
		 background-size: 120px 240px;
	 }

	 .jstree-default-responsive .jstree-file {
		 background: url("../img/40px.png") 0 -160px no-repeat;
		 background-size: 120px 240px;
	 }
	 .jstree-default-responsive .jstree-folder {
		 background: url("../img/40px.png") -40px -40px no-repeat;
		 background-size: 120px 240px;
	 }
 }

 .jstree-default>.jstree-container-ul .jstree-loading>.jstree-ocl {
	 background: url("../img/throbber.gif") center center no-repeat;
 }

 .jstree-default-small>.jstree-container-ul .jstree-loading>.jstree-ocl {
	 background: url("../img/throbber.gif") center center no-repeat;
 }

 .jstree-default-large>.jstree-container-ul .jstree-loading>.jstree-ocl {
	 background: url("../img/throbber.gif") center center no-repeat;
 }

 /*
  * Overriding ember-models-table bootstrap4 styles
  */

 div.models-table-wrapper tr.expanded-row {
	 background: #E3F2FD !important;
	 color: #1E88E5 !important;
 }

 div.models-table-wrapper tr.expanded-row td:first-child {
	 font-size: 1.1rem;
	 font-weight: 900;
 }

 div.models-table-wrapper th {
	 margin: 0rem !important;
	 padding: 0.3rem !important;

	 vertical-align: middle !important;
	 font-weight: 700 !important;
 }

 div.models-table-wrapper td {
	 margin: 0rem !important;
	 padding: 0.3rem !important;

	 vertical-align: middle !important;
 }

 div.models-table-wrapper tr.expand-row > td {
	 padding: 0rem !important;
 }

 div.models-table-wrapper div.table-footer {
	 padding: 2rem 0.5rem 0rem !important;
	 border-top: 1px solid #dedede !important;
 }

 div.models-table-wrapper div.form-inline.globalSearch span.input-group-addon {
	 margin-bottom: 0.5rem;
	 margin-right: 0.5rem;

	 padding-top: 1rem;

	 background-color: unset;
 }

 div.models-table-wrapper div.form-inline.globalSearch input.filterString {
	 margin: 0px;
	 padding: 0px;
 }

 div.models-table-wrapper div.form-inline.globalSearch span.input-group-btn {
	 margin: 0px;
	 padding: 0px;
 }

 div.models-table-wrapper div.form-inline.globalSearch span.input-group-btn button.clearFilterIcon {
	 margin: 0.9rem 0.2rem 0rem;
	 padding: 0rem 0.5rem;
 }

 div.models-table-wrapper div.table-footer a.btn.btn-link {
	 margin: 0rem 0.2rem;
	 padding: 0rem 0.5rem;

	 color: unset !important;
	 display: none;
 }

 div.models-table-wrapper div.table-footer div.btn-group button {
	 margin: 0rem 0.2rem;
	 padding: 0rem 0.5rem;
 }

 div.models-table-wrapper div.table-footer select {
	 display: unset !important;
 }

 /*
 * Overriding Golden Layout colors, etc.
 */

.lm_goldenlayout,
.lm_content {
	background: transparent;
	border: 0px;
}

.lm_header {
	border-bottom: 1px solid #ccc;
	overflow: hidden;
	height: 50px !important;
}

.lm_stack {
	overflow: hidden;
}

.lm_header .lm_tabs {
	margin: 0px !important;
}

.lm_header .lm_tab {
	font-family: "Noto Sans", sans-serif;
	font-size: 1rem;

	background-color: transparent;

	margin: 0px !important;
	border: 0px !important;
	padding: 12px 24px !important;

	cursor: pointer;
}

.lm_header .lm_tab .lm_close_tab {
	background-color: transparent;
	margin-left: 0.25rem;
	margin-top: 0.9rem;
}

.lm_header .lm_controls>li {
	margin-top: 0.9rem;
}

.lm_header .lm_tab.lm_active {
	padding: 12px 24px 8px;
	height: unset;

	-webkit-box-shadow: none;
	box-shadow: none;

	color: #1976d2;
	border-bottom: 2px solid #1976d2;

	cursor: default;
}

.lm_header .lm_tab .lm_title {
	padding-top: 0;
	text-transform: uppercase;
}

.lm_splitter {
	opacity: 0.4;
}
